import React, { useState } from "react";
import DatePicker from "react-datepicker";
function FormWithDisplay() {
  const [formData, setFormData] = useState({
    firstName: "",
    lastName: "",
    dob: null,
    gender: "Male"
  });

  const [selectedDate, setSelectedDate] = useState("");
  const [submitted, setSubmitted] = useState(null);

  const handleInputChange = (event) => {
    const { name, value } = event.target;
    setFormData({
      ...formData,
      [name]: value
    });
  };
  const handleDateChange = (date) => {
    setSelectedDate(date);
  };

  handleGenderChange = (newGender) => {
    // const gender = event.target.value;
    setFormData((prev) => ({
      ...prev,
      gender: newGender
    }));
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    // You can perform any additional validation here if needed
    setSubmitted(formData);
  };

  console.log(submitted, "submit");
  return (
    <div>
      <form onSubmit={handleSubmit}>
        <input
          type="text"
          name="firstName"
          value={formData.firstName}
          onChange={handleInputChange}
          placeholder="enter first name"
        />
        <input
          type="text"
          name="lastName"
          value={formData.lastName}
          onChange={handleInputChange}
          placeholder="enter last name"
        />
        <input
          type="date"
          name="dob"
          value={formData.dob}
          onChange={handleInputChange}
        />
        <input
          type="radio"
          value="Male"
          checked={formData.gender === "Male"}
          onChange={handleInputChange}
          name="gender"
          required
          placeholder="enter gender"
        ></input>
        Male
        <input
          type="radio"
          value="Female"
          onChange={handleInputChange}
          name="gender"
          checked={formData.gender === "Female"}
          placeholder="enter gender"
        ></input>
        Female
        <input
          type="radio"
          value="Other"
          checked={formData.gender === "Other"}
          onChange={handleInputChange}
          name="gender"
          placeholder="enter gender"
        ></input>
        Other
        <button type="submit">Submit</button>
      </form>

      {submitted && (
        <table>
          <thead>
            <tr>
              <th>Name:</th>
              <th>DOB:</th>
              <th>Gender:</th>
              <th>Source</th>
              <th>Destination</th>
              <th>Date of Journey</th>
              <th>Final Price</th>
            </tr>
            <tr>
              <th>{formData.firstName}</th>
              <th>{formData.lastName}</th>
              <th>{formData.dob}</th>
              <th>{formData.gender}</th>
            </tr>
          </thead>
        </table>
      )}
    </div>
  );
}

export default FormWithDisplay;
